html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#navbar-container {
  flex-shrink: 0;
  width: 100%;
}

#scene-container {
  flex-grow: 1;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.text-label-overlay {
  pointer-events: none;
  color: #fff;
  text-shadow: 1px 1px 2px #000;
  width: 100%;
  height: 100%;
  font-family: monospace;
  font-size: 14px;
  position: absolute;
  top: 0;
  left: 0;
}

.label-group {
  background-color: #0003;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  padding: 4px 8px;
  line-height: 1.4;
  display: flex;
  position: absolute;
  transform: translate(-50%);
}

@media (width <= 768px) {
  .lil-gui {
    max-height: 50vh;
    overflow-y: auto;
    transform: translateX(-50%);
    width: 90% !important;
    top: 5px !important;
    left: 50% !important;
    right: 5% !important;
  }

  div.text-overlay div {
    font-size: 16px !important;
  }
}
